import api from "../../../api/index.js"
export default {
  data() {
    return {
      // 语言
      checkedAll: true,
      checkLangList: [],
      checkLists: [{
        id: "1",
        checked: false,
        value: "中文"
      }, {
        id: "2",
        checked: false,
        value: "韩文"
      }, {
        id: "3",
        checked: false,
        value: "英文"
      }, {
        id: "4",
        checked: false,
        value: "日文"
      }],

      // 状态
      checkedAllStatus: true,
      checkStatusList: [],
      checkListsStatus: [{
          id: "1",
          checked: false,
          value: "草稿"
        },
        {
          id: "2",
          checked: false,
          value: "已提交审核"
        },
        {
          id: "3",
          checked: false,
          value: "审核成功"
        },
        {
          id: "4",
          checked: false,
          value: "审核失败"

        }
      ],
      // 品牌名、品牌ID
      brandNameValue: '',
      brandName: "",
      brandID:"",

      searchCondition: "条件",
      searchConditionSelect: '商品名',
      Type01: [{
        value: '选项1',
        label: '中国'
      }, {
        value: '选项2',
        label: '日本'
      }, {
        value: '选项3',
        label: '韩国'
      }],
      Type02: [{
        value: '选项1',
        label: '中国'
      }, {
        value: '选项2',
        label: '日本'
      }, {
        value: '选项3',
        label: '韩国'
      }],
      Type03: [{
        value: '选项1',
        label: '中国'
      }, {
        value: '选项2',
        label: '日本'
      }, {
        value: '选项3',
        label: '韩国'
      }],
      Type01Value: '',
      Type02Value: '',
      Type03Value: '',

      actionDateValue: '创建日期',
      actionDate: [{
        value: '选项1',
        label: '创建日期'
      }, {
        value: '选项2',
        label: '日本'
      }, {
        value: '选项3',
        label: '韩国'
      }],
      pickerOptions2: {
        shortcuts: [{
          text: '今天',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近一周',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近一个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近一年',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 365);
            picker.$emit('pick', [start, end]);
          }
        }]
      },
      tableData: [{
        brandName: '小米',
        spuId: '80000512',
        brandType: '电子产品',
        goodName: '小米滑板车',
        costPrice: '2000',
        unit: '个',
        language: '中文',
        reviewStatus: '审核成功',
        reviewContain: '哈哈哈',

      }, {
        brandName: '小米',
        spuId: '80000512',
        brandType: '电子产品',
        goodName: '小米滑板车',
        costPrice: '2000',
        unit: '个',
        language: '中文',
        reviewStatus: '审核成功',
        reviewContain: '哈哈哈',

      }, {
        brandName: '小米',
        spuId: '80000512',
        brandType: '电子产品',
        goodName: '小米滑板车',
        costPrice: '2000',
        unit: '个',
        language: '中文',
        reviewStatus: '审核成功',
        reviewContain: '哈哈哈',

      }, {
        brandName: '小米',
        spuId: '80000512',
        brandType: '电子产品',
        goodName: '小米滑板车',
        costPrice: '2000',
        unit: '个',
        language: '中文',
        reviewStatus: '审核成功',
        reviewContain: '哈哈哈',

      }],
      value6: '',
      value7: '',
    }
  },
  created(){
    this.showBrandList();
    this.showGoodsList();
  },
  methods: {
    seeDetail() {
      this.$router.push({
        path: '/goodslist/goodsEdit',
      })
    },
    doReview() {
      this.$router.push({
        path: '/goodslist/goodsReview',
      })
    },
    // 显示品牌列表
    showBrandList() {
      const vm = this;
      $.getJSON(api.showBrandList(), function(data, textStatus) {
        console.log(data);
        if (data.code == 2000) {

          console.log(data);
          vm.brandName = data.data;
        }
      });
    },
    // 获取品牌name ID
    getBrandName(){
      this.brandID=event.currentTarget.getAttribute("data-id")
    },

    //获取商品列表
    
    /**
     * checkbox 逻辑
     */
    handleCheck(event) {
      console.log(event);
      if (this.checkedAll) {
        this.checkedAll = !this.checkedAll;
        this.checkLangList = [];
        console.log(event);
        this.checkLangList.push(event.srcElement.defaultValue)
      } else if (this.checkLangList.length == 0) {
        this.checkedAll = !this.checkedAll;
        this.checkLangList = ["全部"];
      }
    },
    handleCheckAll() {
      this.checkedAll = !this.checkedAll;
      if (this.checkedAll) {
        this.checkLangList = ["全部"];
      } else if (this.checkLangList.length == 0) {
        this.checkedAll = !this.checkedAll;
        this.checkLangList = ["全部"];
      }
    },
    handleCheckStatus(event) {
      if (this.checkedAllStatus) {
        this.checkedAllStatus = !this.checkedAllStatus;
        this.checkStatusList = [];
        console.log(event);
        this.checkStatusList.push(event.srcElement.defaultValue)
      } else if (this.checkStatusList.length == 0) {
        this.checkedAllStatus = !this.checkedAllStatus;
        this.checkStatusList = ["全部"];
      }
    },
    handleCheckAllStatus() {
      this.checkedAllStatus = !this.checkedAllStatus;
      if (this.checkedAllStatus) {
        this.checkStatusList = ["全部"];
      } else if (this.checkStatusList.length == 0) {
        this.checkedAllStatus = !this.checkedAllStatus;
        this.checkStatusList = ["全部"];
      }

    },
  }
}




